<template>
  <div class="ltr" :class="{'page-container': !rtl, 'page-container-rtl': rtl}">
    <!-- ### $Topbar ### -->
    <top-bar></top-bar>

    <!-- ### $App Screen Content ### -->
    <router-view></router-view>
  </div>
</template>
<script>
import TopBar from './main/TopBar'
export default {
  name: 'Main',
  components: {
    'top-bar': TopBar
  },
  data () {
    return {
      rtl: true
    }
  }
}
</script>
<style lang="scss">

</style>



