<template>
  <ul class="nav-left" ref="NavLeft">
    <li v-if="!rtl">
      <a id='sidebar-toggle' class="sidebar-toggle" href="javascript:void(0)">
        <i class="ti-menu"></i>
      </a>
    </li>
    <li class="search-box">
      <a class="search-toggle no-pdd-right" href="javascript:void(0)">
        <i class="search-icon ti-search pdd-right-10"></i>
        <i class="search-icon-close ti-close pdd-right-10"></i>
      </a>
    </li>
    <li class="search-input">
      <input class="form-control" type="text" placeholder="Search...">
    </li>
  </ul>
</template>
<script>
import * as $ from 'jquery'
export default {
  name: 'NavLeft',
  components: {
  },
  data () {
    return {
      rtl: true
    }
  },
  mounted () {
    let that = this
    this.$nextTick(function () {
      $(this.$refs.NavLeft).find('.sidebar-toggle').on('click', e => {
        if (that.rtl) $('.app').toggleClass('is-collapsed-rtl')
        else $('.app').toggleClass('is-collapsed')

        e.preventDefault()
      })
      $(this.$refs.NavLeft).find('.search-toggle').on('click', e => {
        $('.search-box, .search-input').toggleClass('active')
        $('.search-input input').focus()
        e.preventDefault()
      })
    })
  }
}
</script>
<style>

</style>



